<template>
    <div
      class="header-wrapper">
        <el-header>
            <el-row
              :gutter="24">
                <el-col
                  :span="12">
                    <div
                      class="header-icon">
                        <img
                          class="header-logo"
                          style="margin-top: 13px;"
                          src="../assets/logo.png"/>
                    </div>
                </el-col>
                <el-col
                  :span="12"
                  style="text-align: right;overflow: hidden;">
                    <div
                      class="fr">
                        <el-link
                          @click="toNotice()"
                          v-if="userInfo.is_station == 1">
                            <i
                              class="el-icon-bell i-mark"><em
                              class="mark"
                              v-show="readStatus != 1"></em></i>
                            消息
                        </el-link>
                        <el-link
                          v-if="userInfo.master"
                          @click="loginBack">
                            <i
                              class="el-icon-refresh"></i>
                            切换
                        </el-link>
                        <el-dropdown
                          @command="handleCommond"
                          trigger="click">
                            <span
                              class="el-dropdown-link txt-center">管理员
                                <i class="el-icon-arrow-down"></i>
                            </span>
                            <el-dropdown-menu
                              slot="dropdown">
