</template>

<script>
export default {
    name: 'Header',
    data() {
        return {
            url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
            search: '',
            userInfo: {},
            readStatus: 0,
            station: '',//检测站
            stationList: [],
        }
    },
    mounted() {
        this.$user.checkLogin(info => {
            this.userInfo = info
        });
    },
    methods: {
        tolink(url) {
            this.$router.push(url)
        },
        goLogin() {
            this.$router.push({path: '/login'})
        },
        handleCommond(command) {
            switch (command) {
                case 'logout':
                    this.logout();
                    break;
                case 'reset_password':
                    this.$router.push('/user/password');
                    break;
                case 'reset_avator':
                    this.$router.push('/user/avator');
                    break;
            }
        },
        logout() {
            this.$message.confirm({message: '确定要退出登录吗'}, () => {
                this.$loading.show('退出登录中')
                this.$user.logout(() => {
                    this.$loading.close()
                    this.$store.commit('menuRouteLoaded', false)
                    this.$router.push('/login')
                })
            })
        },

        goBack() {
            console.log('go back');
        },

        toNotice() {
            this.$router.push('/notice');
        },
        //获取公告列表读取状态
        getReadStatus() {
            this.$api.management.readNoticeStatus().then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                } else {
                    this.readStatus = result.data.all_read;
                }
            }).catch(error => {
                this.$message.error(error);
            });
        },
    }
}
</script>
