<template>
    <div
        class="nav sidebar nj-nav">
        <div
            class="nj-nav-menu">
            <ul>
                <el-col
                    v-for="(item,index) in menuList"
                    :key="index">
                    <li
                        @click="showSubs(item,index)"
                        :class="index===curPage?'active':''">
                        <i :class="item.ico"></i>
                        <div
                            class="mt-5">
                            {{ item.name }}
                        </div>
                        <i class="el-icon-caret-left"></i>
                    </li>
                </el-col>
                <el-col>
                    <li @click="goDocument">
                        <i class="el-icon-document"></i>
                        <div
                            class="mt-5">
                            文档
                        </div>
                        <i class="el-icon-caret-left"></i>
                    </li>
                </el-col>
            </ul>
        </div>
    </div>
</template>

<script>
import bus from '../assets/js/bus';

export default {
    name: 'Nav',
    data() {
        return {
            isCollapse: false,
            itemId: 0,
            items: [],
            curPage: 0,
            menuList: [
                {
                    id: 1,
                    name: "节点",
                    ico: "el-icon-s-platform",
                    path: "/server/status",
                    group_name: "节点管理",
                    childrens: []
                },
                {
                    id: 2,
                    name: "任务",
                    ico: "el-icon-alarm-clock",
                    path: "/server/crontab",
                    group_name: "后台任务",
                    childrens: []
                },
                {
                    id: 3,
                    name: "队列",
                    ico: "el-icon-s-operation",
                    path: "/server/queue",
                    group_name: "redis队列",
                    childrens: []
                },
                {
                    id: 4,
                    name: "数据",
                    ico: "el-icon-coin",
                    path: "/database/maintain",
                    group_name: "数据维护",
                    childrens: []
                },
                // {
                //     id: 99,
                //     name: "amis",
                //     ico: "el-icon-present",
                //     path: "/amis/demo",
                //     group_name: "amis",
                //     childrens: []
                // }
            ],
        }
    },
    methods: {
        goDocument(){
            let routeData = this.$router.resolve({path: '/document/ws', query: {}});
            window.open(routeData.href, '_blank');
        },
        showSubs(item, index) {
            this.curPage = index;
            this.$router.push(item.path);
        },
        render() {
            this.menuList.map((val, index) => {
                if (val.childrens.length > 0) {
                    val.childrens.map((sval, sindex) => {
                        if (sval.path === this.$route.path) {
                            this.curPage = index;
                        }
                    })
                } else {
                    if (val.path === this.$route.path) {
                        this.curPage = index;
                    }
                }
            });
        }
    },
    mounted() {
        this.render()
    },
    created() {
        // 通过 Event Bus 进行组件间通信，来折叠侧边栏
        bus.$on('collapse', msg => {
            this.collapse = msg;
        })
    },
    updated() {
        this.render();
    },
    computed: {
        onRoutes() {
            return this.$route.path.replace('/', '');
        }
    },
    watch: {
        $route(to) {
            this.render();
        }
    }
}
</script>

