<template>
    <div>
        <css href="//at.alicdn.com/t/font_1896021_37x41oa4sxj.css"/>
        <el-container>
            <el-header>
                <Header/>
            </el-header>
            <el-container>
                <el-aside :style="{'width':sideBarWidth+'px'}">
                    <Nav/>
                </el-aside>
                <el-main>
                    <div class="app-container" ref="container">
                        <router-view/>
                    </div>
                </el-main>
            </el-container>
        </el-container>
    </div>

</template>
<script>
import Header from '../components/Header.vue';
import Nav from '../components/Nav.vue';
import bus from '../assets/js/bus';

export default {
    name: "Index",
    data() {
        return {
            sideBarWidth: 80,
            leftWidth: 100,
            showLogoutAlert: false,
            promptTimes: 0,
            isLoading: false,
            topH: 0
        }
    },
    components: {
        Header,
        Nav
    },
    mounted() {
        window.addEventListener('scroll', this.handleScroll, true)
    },
    methods: {
        handleScroll() {
            this.$store.commit('setIndexTop', this.$refs.container.offsetTop)
            this.$store.commit('setEditTop', this.$refs.container.getBoundingClientRect().top)
        }
    },
    watch: {
        $route(newValue, oldValue) {
            this.$store.commit('setRoutePath', {
                current: newValue.fullPath,
                previous: oldValue.fullPath
            });
        }
    },
    created() {
        bus.$on('collapse', msg => {
            this.collapse = msg;
        })

        // 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
        bus.$on('tags', msg => {
            let arr = [];
            for (let i = 0, len = msg.length; i < len; i++) {
                msg[i].name && arr.push(msg[i].name);
            }
            this.tagsList = arr;
        })
    },
    beforeDestroy() {
        window.removeEventListener("scroll", this.handleScroll)
    }
}
</script>

